import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CdkTableModule } from '@angular/cdk/table';
import {
    MatButtonModule,
    MatCheckboxModule,
    MatNativeDateModule,
    MatDatepickerModule,
    MatFormFieldModule,
    MatIconModule,
    MatInputModule,
    MatMenuModule,
    MatPaginatorModule,
    MatRippleModule,
    MatSidenavModule,
    MatSlideToggleModule,
    MatSortModule,
    MatTabsModule,
    MatTableModule,
    MatToolbarModule
} from '@angular/material';

import { FuseSharedModule } from '@fuse/shared.module';
import { FuseConfirmDialogModule } from '@fuse/components';
import { SharedModule } from '../../shared/shared.module';

import { WebstoreService } from './webstore.service';

import { FuseWebstoreComponent } from './webstore/webstore.component';
import { FuseWebstoreListComponent } from './webstore-list/webstore-list.component';
import { FuseWebstoreFormComponent } from './webstore-form/webstore-form.component';

const routes: Routes = [
    {
        path: '**',
        component: FuseWebstoreComponent,
        resolve  : {
            webstore: WebstoreService
        }
    }
];

@NgModule({
    declarations   : [
        FuseWebstoreComponent,
        FuseWebstoreListComponent,
        FuseWebstoreFormComponent,
    ],
    imports        : [
        RouterModule.forChild(routes),
        CdkTableModule,

        MatButtonModule,
        MatCheckboxModule,
        MatNativeDateModule,
        MatDatepickerModule,
        MatFormFieldModule,
        MatIconModule,
        MatInputModule,
        MatMenuModule,
        MatPaginatorModule,
        MatRippleModule,
        MatSidenavModule,
        MatSlideToggleModule,
        MatSortModule,
        MatTabsModule,
        MatTableModule,
        MatToolbarModule,

        FuseSharedModule,
        FuseConfirmDialogModule,
        SharedModule
    ],
    providers      : [
        WebstoreService
    ],
    entryComponents: [
        FuseWebstoreFormComponent
    ]
})
export class FuseWebstoreModule
{
}



